body {
    font-family: 'Lato', sans-serif;
    font-size: 16px;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  .container {
    display: flex;
    width: 100%;
    min-height: 100vh;
    height: 100%;
  }
  
  .side-bar {
    width: 250px;
    box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
    z-index: 1;
    position: relative;
  }
  
  .inputStyle {
    width: 3rem !important;
    height: 3rem;
    margin: 0 1rem;
    font-size: 2rem;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, 0.3);
  }
  
  @media only screen and (max-width: 678px) {
    .container {
      flex-wrap: wrap;
      flex-direction: column;
    }
  
    .side-bar {
      width: 100%;
      display: flex;
      flex-direction: column;
    }
  
    .side-bar__segment--header {
      text-align: center;
    }
  
    .side-bar__segment--bottom {
      position: static !important;
      margin: 10px;
    }
  
    label {
      display: block;
      float: left;
      width: 120px;
    }
  
    .card {
      width: 100%;
      min-width: 120px !important;
      margin: 20px;
    }
  
    .side-bar__segment > label > input[type='number'],
    .side-bar__segment > label > input[type='text'] {
      width: 180px !important;
    }
  }
  
  @media only screen and (max-width: 470px) {
    .margin-top--small .inputStyle {
      width: 2rem !important;
      height: 2rem;
      margin: 0 5px;
      font-size: 1rem;
    }
  
    .card > form > p {
      font-size: 1.2em !important;
    }
  }
  
  .side-bar__segment {
    padding: 0.5rem 1rem;
  }
  
  .side-bar__segment--header {
    margin: 1rem 0;
    border-bottom: 1px solid lightgray;
    border-top: 1px solid lightgray;
  }
  
  .side-bar__segment--header:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
  
  .side-bar__segment--bottom {
    font-weight: bold;
    position: absolute;
    bottom: 2rem;
  }
  
  .view {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: #f2f2f3;
  }
  
  .side-bar__segment > label > input[type='number'],
  .side-bar__segment > label > input[type='text'] {
    width: 150px;
    border-radius: 4px;
    border: 1px solid lightgrey;
    margin: 0.5rem 0;
    height: 1.5rem;
    padding: 0 0.5rem;
  }
  
  .side-bar__segment > label > input[type='checkbox'] {
    border-radius: 4px;
    border: 1px solid lightgrey;
    margin: 0.5rem;
    width: 1rem;
    height: 1rem;
  }
  
  .card {
    min-width: 360px;
    padding: 2rem;
    box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
    border-radius: 4px;
    background: white;
  }
  
  .card > form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .card > form > p {
    font-size: 1.5em;
    font-weight: bold;
  }
  
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    appearance: none;
    margin: 0;
  }
  
  input[type='number'] {
    -webkit-appearance: textfield;
    appearance: textfield;
  }
  
  .btn-row {
    display: flex;
    flex-direction: row;
  }
  
  .btn {
    background-color: #3273dc;
    border: none;
    padding: 0.5rem 1rem;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    margin: 1rem;
  }
  
  .btn:disabled {
    background-color: rgba(0, 0, 0, 0.4);
    cursor: not-allowed;
  }
  
  .margin-top--small {
    margin-top: 1rem;
    margin-bottom: -1rem;
  }
  
  .margin-top--small > div {
    flex-wrap: wrap;
  }
  
  .margin-top--small > div > div {
    margin-bottom: 1rem;
  }
  
  .margin-top--medium {
    margin-top: 1.5rem;
  }
  
  .margin-top--large {
    margin-top: 4rem;
  }
  
  .error {
    border: 1px solid red !important;
  }
  body {
    margin: 0%;
    padding: 0%;
  }
  
  .chat-window {
    width: 100%;
    height: 100%;
  }
  
  .chat-window p {
    margin: 0;
  }
  .chat-window .chat-header {
    height: 55px;
    /* border-radius: 6px; */
    background: #263238;
    position: relative;
    cursor: pointer;
  }
  .chat-window .chat-header p {
    display: block;
    padding: 0 1em 0 2em;
    color: #fff;
    font-weight: 700;
    line-height: 55px;
  }
  
  .chat-window .chat-body {
    height: calc(88vh - (55px + 70px));
    border: 1px solid #263238;
    background: rgb(255, 255, 255);
  
    position: relative;
  }
  .chat-window .chat-body .message-container {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  
  .chat-window .chat-body .message-container::-webkit-scrollbar {
    display: none;
  }
  .chat-window .chat-body .message {
    height: auto;
    padding: 10px;
    display: flex;
  }
  
  .chat-window .chat-body .message .message-content {
    width: auto;
    height: auto;
    min-height: 40px;
    max-width: 200px;
    background-color: #43a047;
    /* border-radius: 5px; */
    color: white;
    display: flex;
    align-items: center;
    margin-right: 5px;
    margin-left: 5px;
    padding-right: 5px;
    padding-left: 5px;
    overflow-wrap: break-word;
    word-break: break-word;
  }
  
  #you {
    justify-content: flex-end;
  }
  
  #you .message-content {
    justify-content: flex-start;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  
  #you .message-meta {
    justify-content: flex-start;
    margin-left: 5px;
    padding-left: 5px;
  }
  
  #other {
    justify-content: flex-start;
    
  }
  
  #other .message-content {
    justify-content: flex-start;
    background-color: #3f51b5;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  
  #other .message-meta {
    justify-content: flex-end;
    margin-right: 5px;
    padding-right: 5px;
  }
  
  .message-meta #author {
    margin-left: 10px;
    font-weight: bold;
  }
  
  .chat-window .chat-body .message .message-meta {
    display: flex;
    font-size: 12px;
  }
  
  .chat-window .chat-footer {
    height: 40px;
    border: 1px solid #263238;
    border-top: none;
    display: flex;
  }
  
  .chat-window .chat-footer input {
    height: 100%;
    flex: 85% 1;
    border: 0;
    padding: 0 0.7em;
    font-size: 1em;
    border-right: 1px dotted #607d8b;
  
    outline: none;
    font-family: "Open Sans", sans-serif;
  }
  
  .chat-window .chat-footer button {
    border: 0;
    display: grid;
    place-items: center;
    cursor: pointer;
    flex: 15% 1;
    height: 100%;
    background: transparent;
    outline: none;
    font-size: 25px;
    color: lightgray;
  }
  
  .chat-window .chat-footer button:hover {
    background: #c0ae4a;
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  .hide {
    opacity: 0 !important;
  }

 
